.main__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 6px 0 6px 0;
}

.logo__name a{
    font-family: "Playfair Display", serif;
    font-size: 42px;
    font-weight: 800;
    color: black;
    text-decoration: none;
}

.menu__list ul{
    display: flex;
    justify-content: space-between;
}

.menu__list li{
    list-style-type: none;
}

.menu__list a{
    text-decoration: none;
    color: #121212;
    font-size: 14px;
    font-weight: 500;
    padding: 27px 29px 25px 29px;
    transition: .5s;
}

.menu__list a:hover{
    background-color: #121212;
    color: white;
    transition: .5s;
}

.menu__list li:last-child{
    margin-right: 0;
}

.header__buttons{
    display: flex;
    justify-content: space-between;
}

.header__buttons div{
    margin-right: 27px;
}

.dropdown__menu{
    border-top: 1px solid #121212;
    position: absolute;
    height: 336px;
    width: 100%;
    z-index: 2;
    background-color: #fff;
    padding: 34px 0;
    display: none;
}

.main__header-link{
    cursor: pointer;
    position: relative;
}

.main__header-link .total__products{
    width: 14px;
    height: 14px;
    color: white;
    background-color: #121212;
    font-size: 9px;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 2px;
    font-weight: 700;
}
@media (max-width: 1430px) {
    .logo__name a{
        font-size: 38px;
        font-weight: 800;
    }
    .menu__list a{
        font-size: 13px;
        padding: 27px 29px 25px 29px;
    }
    .header__buttons div{
        width: 16px;
        height: 16px;
        margin-bottom: 10px;
    }
}

@media (max-width: 990px) {
    .navigation{
        display: none;
    }
}

@media screen and (max-width: 479px) {
    .logo__name{
        margin-left: -5%;
    }
    .logo__name a {
        font-size: 28px;
    }
    .menu__list a{
        display: none;
    }
    .header__buttons div{
        width: 10px;
        height: 10px;
    }
}



@media (min-width: 480px) and (max-width: 767px) {
    .logo__name a {
        font-size: 40px;
    }
    .menu__list a{
        display: none;
    }
    .header__buttons div{
        width: 10px;
        height: 10px;
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .logo__name{
        margin-left: -20%;
    }
    .logo__name a {
        font-size: 40px;
    }
    .menu__list a{
        display: none;
    }
    .header__buttons div{
        width: 10px;
        height: 10px;
    }
}

@media( min-width: 1024px ) and (max-width: 1439px){
    .menu__list a{
        font-size: 12px;
        padding: 27px 29px 25px 29px;
    }
    .header__buttons div{
        width: 10px;
        height: 10px;
    }
    .logo__name a {
        font-size: 40px;
    }
    .menu__list ul li:nth-child(1), .menu__list ul li:nth-child(6), .menu__list ul li:nth-child(7){
        display: none;
    }
}



