.order__page{
    padding-top: 120px;
}

.order__page-totalPrice button{
    padding: 17px;
    width: 250px;
    background-color: #121212;
    border: 2px solid #121212;
    color: white;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 500;
    opacity: 0.9;
    margin-top: 21px;
    cursor: pointer;
    margin-bottom: 30px;
}

@media (min-width: 0px) and (max-width: 767px) {
    .order__page{
        padding-top: 90px;
    }
}
